.form-container button{
  margin-top: 10px;
  border: 0;
  border-radius: 4px;
  padding: 0 20px 0 20px;
  height: 48px;
  font-size: 16px;
  background: linear-gradient(to right, #1ee64b, #2565db);
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  opacity: 0.9;
}

.form-container button:hover{
  opacity: 1;
  transition: all .5s;
}

.form-container .cancel{
  margin-top: 10px;
  border: 0;
  border-radius: 4px;
  padding: 0 20px 0 20px;
  height: 48px;
  font-size: 16px;
  background: linear-gradient(to right, #723939, #ec3c3c);
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  opacity: 0.9;
}

.form-container .cancel:hover{
  opacity: 1;
  transition: all .5s;
}

.form-container .del{
  margin-top: 10px;
  border: 0;
  border-radius: 4px;
  padding: 0 20px 0 20px;
  height: 48px;
  font-size: 16px;
  background: #fff;
  border: 1px solid #f54d4d;
  font-weight: bold;
  color: #5c5959;
  cursor: pointer;
  opacity: 0.9;
}

.modal-dialog-link {
  max-width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.modal-content-link {
  height: auto;
  min-height: 100%;
