.login-container{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-container form > img{
  height: 100px;
  margin-left: -10px;
}

.login-container form{
  width: 100%;
  max-width: 350px;
  display: flex;
  flex-direction: column;
}

.login-container form input{
  margin-top: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  height: 48px;
  padding: 0 20px;
  font-size: 16px;
  color: #666;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,0.1)!important
}

#login-alert{
  box-shadow: 0 .5rem 1rem rgba(0,0,0,0.1)!important
}

.login-container form input::placeholder{
  color: #999;
}

.login-container form button{
  margin-top: 10px;
  border: 0;
  border-radius: 4px;
  height: 48px;
  font-size: 16px;
  background: linear-gradient(to right, #1ee64b, #2565db);
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  opacity: 0.9;
}

.login-container form button:hover{
  opacity: 1;
  transition: all .5s;
}

.btn-link-disabled{
  cursor:not-allowed;
}